    }
    span {
      display: block;
      font-weight: bold;
      font-size: .6rem;
      color: #878787;
      text-align: center;
    }
  }
}
</style>

<template>
  <div class="account">
    <div class="account-top">
      <div class="account-img">
        <img src="../../assets/img/toxian.jpg" />
      </div>
    </div>
    <div class="account-center">
      <ul class="account-center_ul">
        <li>
          <p>我的矿机数量</p>
          <span>13</span>
        </li>
        <li>
          <p>矿机总算力</p>
          <span>13</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {

}
</script>


